const watermarkStyle = {
  color: 'rgba(11, 94, 29, 0.2)',
  visible: true,
  text: 'blep.ai',
  fontSize: 48,
  horzAlign: 'center',
  vertAlign: 'center',
}

export default class Charts extends Component {
  constructor (props) {
    super(props)

    /* https://fapi.binance.com/futures/data/openInterestHist/?symbol=BTCUSDT&period=5m&limit=300
    [
      {"symbol":"BTCUSDT",
      "sumOpenInterest":"35024.38800000",
      "sumOpenInterestValue":"240368648.56573727",
      "timestamp":1587528000000}
    ] */

    this.handleChange = this.handleChange.bind(this)

    this.state = {
      exchange: 'binance',
      period: '5m', // "5m","15m","30m","1h","2h","4h","6h","12h","1d"
      symbol: 'BTCUSDT',
      limit: 300,
      options: {
        alignLabels: true,
        timeScale: {
          rightOffset: 0,
          barSpacing: 3,
          fixLeftEdge: false,
          lockVisibleTimeRangeOnResize: false,
          rightBarStaysOnScroll: false,
          borderVisible: true,
          borderColor: '#2b2b43',
          visible: true,
          timeVisible: true,
          secondsVisible: false,
        },
        layout: {
          fontFamily: 'Heebo',
        },
        priceScale: {
          // position: 'right',
          // mode: 0,
          // autoScale: false,
          // invertScale: true,
          // alignLabels: false,
          // borderVisible: false,
          // borderColor: '#555ffd',
          /* scaleMargins: {
            top: 0.30,
            bottom: 0.25,
          }, */
        },
        watermark: { ...watermarkStyle },
      },
      sumOpenInterest: [{ data: [] }],
      sumOpenInterestValue: [{ data: [] }],
      topLongShortAccountRatio_longAccount: [{ data: [] }],
      globalLongShortAccountRatio_longAccount: [{ data: [] }],
      topLongShortPositionRatio_longAccount: [{ data: [] }],
      buySellRatio: [{ data: [] }],
      buySellVol: [{ data: [] }],
    }
  }

  // raw = from binance, keys = the keys to extract and add timestamp to
  extractLineSeries (raw, ...keys) {
    const data = {}
    for (const k of keys) {
      data[k] = []
    }

    raw.forEach(d => {
      for (const key in data) {
        data[key].push({
          time: d.timestamp / 1000,
          value: parseFloat(d[key]),
        })
      }
    })

    return data
  }

  async makeReq (endpoint) {
    const proxy = 'https://blep-cors.herokuapp.com'
    const bnbAPI = 'https://fapi.binance.com'
    const symbol = this.state.symbol
    const limit = this.state.limit

    try {
      const res = await fetch(`${proxy}/${bnbAPI}${endpoint}/?symbol=${symbol}&period=${this.state.period}&limit=${limit}`)
      if (!res.ok) throw Error(res.statusText)
      return res.json()
    } catch (error) {
      console.err(error)
    }
  }

  handleChange (event) {
    // this.setState({[event.target.name]: event.target.value});
    this.state[event.target.name] = event.target.value
    this.componentDidMount()
  }

  async componentDidMount () {
    const endpoints = [
      '/futures/data/openInterestHist',
      '/futures/data/topLongShortAccountRatio',
      '/futures/data/globalLongShortAccountRatio',
      '/futures/data/topLongShortPositionRatio',
      '/futures/data/takerlongshortRatio',
    ]
    const json = await Promise.all(endpoints.map(this.makeReq.bind(this)))

    let data = this.extractLineSeries(json[0], 'sumOpenInterest', 'sumOpenInterestValue')

    for (const key in data) {
      this.setState({ [key]: [{ data: data[key] }] })
    }

    // ----
    data = this.extractLineSeries(json[1], 'longAccount')
    const ones = data.longAccount.map(d => {
      return {
        time: d.timestamp / 1000,
        value: 1,
      }
    })
    const zeros = data.longAccount.map(d => {
      return {
        time: d.timestamp / 1000,
        value: 0,
      }
    })

    for (const key in data) {
      this.setState({ [`topLongShortAccountRatio_${key}`]: [{ data: data[key] }, { data: ones }, { data: zeros }] })
    }

    // ----
    data = this.extractLineSeries(json[2], 'longAccount')

    for (const key in data) {
      this.setState({ [`globalLongShortAccountRatio_${key}`]: [{ data: data[key] }, { data: ones }, { data: zeros }] })
    }

    // ----
    data = this.extractLineSeries(json[3], 'longAccount')

    for (const key in data) {
      this.setState({ [`topLongShortPositionRatio_${key}`]: [{ data: data[key] }, { data: ones }, { data: zeros }] })
    }

    // ----
    data = this.extractLineSeries(json[4], 'buySellRatio', 'buyVol', 'sellVol')

    data.buyVol = data.buyVol.map(d => ({ ...d, color: 'green' }))
    data.sellVol = data.sellVol.map(d => ({ ...d, value: -d.value, color: 'red' }))

    this.setState({ buySellRatio: [{ data: data.buySellRatio }] })
    this.setState({ buySellVol: [{ data: data.buyVol }, { data: data.sellVol }] })
  }

  get charts () {
    const charts = [
      ['Open Interest', this.state.sumOpenInterest],
      ['Open Value', this.state.sumOpenInterestValue],
      ['Top Account Longs+Shorts', this.state.topLongShortAccountRatio_longAccount, 'areaSeries'],
      ['Account Longs+Shorts', this.state.globalLongShortAccountRatio_longAccount, 'areaSeries'],
      ['Top Positions Longs+Shorts', this.state.topLongShortPositionRatio_longAccount, 'areaSeries'],
      ['Buy Sell Volume Ratio', this.state.buySellRatio],
      ['Buy Sell Volume', this.state.buySellVol, 'histogramSeries'],
    ]
    const titleize = (title) => `${title} ${this.state.period} | blep.ai`
    return charts.map(([title, data, type = 'lineSeries']) => (
      <Chart key={title} className='py-12 my-12' options={{ ...this.state.options, watermark: { ...watermarkStyle, text: titleize(title) } }} {...{ [type]: data }} autoWidth height={600} />
    ))
  }

  render () {
    return (
      <Layout includePreFooter>
        <div className='flex flex-col bg-white'>
          <div className='container px-8 mx-auto'>
            <main className='py-12 my-12'>
              <label>
                Exchange
                <select name='exchange' value={this.state.period} onChange={this.handleChange}>
                  <option value='binance'>Binance</option>
                </select>
              </label>
              <label>
                Period
                <select name='period' value={this.state.period} onChange={this.handleChange}>
                  <option value='5m'>5m</option>
                  <option value='15m'>15m</option>
                  <option value='30m'>30m</option>
                  <option value='1h'>1h</option>
                  <option value='2h'>2h</option>
                  <option value='4h'>4h</option>
                  <option value='6h'>6h</option>
                  <option value='12h'>12h</option>
                  <option value='1d'>1d</option>
                </select>
              </label>

              {this.charts}
            </main>
          </div>
        </div>
      </Layout>
    )
  }
}
